/* eslint-disable react/no-array-index-key */
/* eslint-disable no-shadow */
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Controller, useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import * as Yup from 'yup'
import { useTranslation } from 'react-i18next'
import { TRANSLATE_NS } from 'src/constants'
import { useSnackbar } from 'notistack'
import {
  Box,
  Button,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core'
import { updateInformationForm } from 'src/actions/setsActions'
import SplashScreen from 'src/components/SplashScreen'
import setsService from 'src/services/setsService'

const textFields = [
  { name: "name", multiple: false, label: "sets:form1.name", default: "" },
  { name: "description", multiple: true, label: "sets:form1.detail", default: "" }
]

const useStyles = makeStyles(() => ({
  root: {},
}))

function SurgicalSetDetails({
  className,
  onBack,
  onNext,
  ...rest
}) {
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.ERRORS])
  const dispatch = useDispatch()
  const {set} = useSelector(state=> state.setCreate)
  const { enqueueSnackbar } = useSnackbar()

  const schema = Yup.object().shape({
    name: Yup.string().required(t('errors:required')).min(3, t('errors:must be at least 3 characters')).max(255),
    description: Yup.string(),
    active: Yup.bool()
  })


  const { handleSubmit, errors, control, formState, reset } = useForm({
    mode: "onChange",
    defaultValues: {
      name: "",
      description: "",
      active: false
    },
    resolver: yupResolver(schema)
  })

  // Read the formState before render to subscribe the form state through Proxy
  const { isDirty, isSubmitting } = formState 

  useEffect(()=> {
    if(set){
      reset({...set})
    }
  }, [set, reset])

  const onSubmit = handleSubmit(async (data) => {
    if(isDirty === false){
      if(onNext){
        onNext()
      }
      return
    }
    const callback = (response) => {
      if(response.resultFlag === false){
        enqueueSnackbar(response.errorMessage, { variant: 'error' })
        return
      }
      enqueueSnackbar('Create success!', { variant: 'success' })
      
      dispatch(updateInformationForm(response.value))
      if(onNext){
        onNext()        
        reset()
      }
    }
    if(!data.id){
      setsService.insert(data).then(callback)
    }
    else{
      setsService.update(data).then(callback)
    }
  })
  
  const hasError = inputName => !!(errors && errors[inputName])
  const errorMessage = inputName => errors[inputName]?.message ?? null

  if(!set){
    return <SplashScreen />
  }

  return (
    <form
      data-testid="form"
      onSubmit={onSubmit}
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Typography
        variant="h3"
        color="textPrimary"
      >
        {t('sets:surgical instrument sets')}
      </Typography>
      <Box mt={2}>
        <Typography
          variant="body2"
          color="textSecondary"
        >
          {t('sets:form1.description text')}
        </Typography>
      </Box>
      <Box mt={2}>
        <Typography
          variant="subtitle1"
          color="textSecondary"
        >
        </Typography>
      </Box>

      {textFields.map(field=> (
        <Box mt={2} key={field.name}>
          {!field.multiple ? (
            <Controller               
              data-testid={`txt-${field.name}`}
              name={field.name}
              control={control}
              render={(props) =>(
                <TextField
                  error={hasError(field.name)}
                  helperText={errorMessage(field.name)}
                  variant="outlined"
                  fullWidth
                  label={t(field.label)}
                  value={props.value}
                  {...props}
                  inputProps={{
                    'data-testid': `txt-${field.name}`
                  }}
                />
              )}
            />
          ): (
            <Controller 
              name={field.name}
              control={control}
              render={(props) =>(
                <TextField
                  error={hasError(field.name)}
                  helperText={errorMessage(field.name)}
                  variant="outlined"
                  fullWidth
                  label={t(field.label)}
                  value={props.value}
                  multiline
                  rows={2}
                  rowsMax={5}
                  {...props}
                  inputProps={{
                    'data-testid': `txt-${field.name}`
                  }}
                />
              )}
            />
          )}
        </Box>
      ))}
      <Box mt={2}>
        <Typography
          variant="h6"
          color="textPrimary"
        >
          {t('sets:form1.label for the active switch')}
        </Typography>
        <Typography
          variant="body2"
          color="textSecondary"
        >
          {t('sets:form1.description for the active switch')}
        </Typography>
        <Controller
          name="active"
          control={control}
          render={(props) => (
            <Switch
              onChange={(e) => props.onChange(e.target.checked)}
              checked={props.value}
              inputProps={{
                'data-testid': 'cbx-active'
              }}
            />
          )}
        />
      </Box>
      <Box
        mt={3}
        display="flex"
      >
        {onBack && (
          <Button
            onClick={onBack}
            size="large"
          >
            {t('sets:button.previous')}
          </Button>
        )}
        <Box flexGrow={1} />
        <Button
          disabled={isSubmitting}
          color="primary"
          type="submit"
          variant="contained"
          size="medium"
          data-testid="submit"
        >
          {t('sets:button.next')}
        </Button>
      </Box>
    </form>
  )
}

SurgicalSetDetails.propTypes = {
  className: PropTypes.string,
  onNext: PropTypes.func,
  onBack: PropTypes.func
}

export default SurgicalSetDetails