import React, { useEffect, useRef, useState } from 'react'
import { useDispatch } from 'react-redux'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { Link as RouterLink } from 'react-router-dom'
import {
  Avatar,
  Box,
  Button,
  Card,
  CardContent,
  Container,
  Grid,
  Paper,
  Step,
  StepConnector,
  StepLabel,
  Stepper,
  Typography,
  makeStyles,
  withStyles,
  colors
} from '@material-ui/core'
import {
  Star as StarIcon,
} from 'react-feather'
import { useTranslation } from 'react-i18next'
import { TRANSLATE_NS } from 'src/constants'
import {resetForm, initCreate} from 'src/actions/setsActions'
import setsService from 'src/services/setsService'
import Page from 'src/components/Page'
import SelectEquipments from './SelectEquipments'
import Detail from './Detail'

const steps = [
  {
    label: 'ข้อมูลการผ่าตัด'
  },
  {
    label: 'เลือกเครื่องมือทางการแพทย์'
  }
]

const CustomStepConnector = withStyles((theme) => ({
  vertical: {
    marginLeft: 19,
    padding: 0,
  },
  line: {
    borderColor: theme.palette.divider
  }
}))(StepConnector)

const useCustomStepIconStyles = makeStyles((theme) => ({
  root: {},
  active: {
    backgroundColor: theme.palette.secondary.main,
  },
  completed: {
    backgroundColor: theme.palette.secondary.main,
  }
}))

function CustomStepIcon({ active, completed, icon }) {
  const classes = useCustomStepIconStyles()
  const stepNumber = icon
  return (
    <Avatar
      className={clsx(classes.root, {
        [classes.active]: active,
        [classes.completed]: completed,
      })}
    >
      {
        stepNumber
      }
    </Avatar>
  )
}

CustomStepIcon.propTypes = {
  active: PropTypes.bool,
  completed: PropTypes.bool,
  icon: PropTypes.number
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.default,
    minHeight: '100%',
    padding: theme.spacing(2, 0),
  },
  avatar: {
    backgroundColor: colors.red[600]
  },
}))

function SurgicalSetCreateView({match}) {
  const classes = useStyles()
  const {t} = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS])
  const [activeStep, setActiveStep] = useState(0)
  const [completed, setCompleted] = useState(false)
  const dispatch = useDispatch()
  const isFirst = useRef(true)  
  const setId = match.params?.id

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1)
  }

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1)
  }

  const handleComplete = () => {
    setCompleted(true)
  }

  useEffect(()=> {
    
    async function getById(){
      const dataItem = await setsService.getById(setId)
      await dispatch(initCreate(dataItem?.value))
    }

    if(isFirst.current){
      if(!setId){
        dispatch(initCreate(null))
      }
      else{
        getById()
      }      
      isFirst.current = false
    }

    return () => {
      dispatch(resetForm())
    }
  }, [dispatch, setId])

  return (
    <Page
      className={classes.root}
      title={!setId 
        ? t('sets:add surgical instrument sets')
        : t('sets:edit surgical instrument sets')}
    >
      <Container maxWidth="lg">
        <Box 
          mb={1}
          display="flex"
        >
          <Box 
            flexGrow={1}
          >
            <Typography
              variant="h3"
              color="textPrimary"
            >
            {!setId 
              ? t('sets:add surgical instrument sets')
              : t('sets:edit surgical instrument sets')}
            </Typography>
          </Box>        
          <Button
            variant="contained"
            size="medium"
            color="primary"
            component={RouterLink}
            to="/sets"
          >
            {t('sets:button.exit')}
          </Button>
        </Box>
        {!completed ? (
          <Paper>
            <Grid container>
              <Grid
                item
                xs={12}
                md={2}
              >
                <Stepper
                  activeStep={activeStep}
                  connector={<CustomStepConnector />}
                  orientation="vertical"
                  component={Box}
                  bgcolor="transparent"
                >
                  {steps.map((step) => (
                    <Step key={step.label}>
                      <StepLabel StepIconComponent={CustomStepIcon}>
                        {step.label}
                      </StepLabel>
                    </Step>
                  ))}
                </Stepper>
              </Grid>
              <Grid
                item
                xs={12}
                md={10}
              >
                <Box p={3} >
                  {activeStep === 0 && (
                    <Detail onNext={handleNext} />
                  )}
                  {activeStep === 1 && (
                    <SelectEquipments onBack={handleBack} onComplete={handleComplete} />
                  )}
                </Box>
              </Grid>
            </Grid>
          </Paper>
        ) : (
          <Card>
            <CardContent>
              <Box
                maxWidth={450}
                mx="auto"
              >
                <Box
                  display="flex"
                  justifyContent="center"
                >
                  <Avatar className={classes.avatar}>
                    <StarIcon />
                  </Avatar>
                </Box>
                <Box mt={2}>
                  <Typography
                    variant="h3"
                    color="textPrimary"
                    align="center"
                  >
                    {t('sets:you are all done')}
                  </Typography>
                </Box>
                <Box mt={2}>
                  <Typography
                    variant="body2"
                    color="textSecondary"
                    align="center"
                  >
                    {t('sets:message')}
                  </Typography>
                </Box>
                <Box
                  mt={2}
                  display="flex"
                  justifyContent="center"
                >
                  <Button
                    variant="contained"
                    color="secondary"
                    component={RouterLink}
                    to="/sets"
                  >
                    {t('sets:view your surgical instruments')}
                  </Button>
                </Box>
              </Box>
            </CardContent>
          </Card>
        )}
      </Container>
    </Page>
  )
}

export default SurgicalSetCreateView