import React from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import {  
  Box, 
  IconButton, 
  ListItem,
  ListItemIcon,
  Checkbox,
  Typography,
  makeStyles,
  FormControlLabel,
  Tooltip,
  } from '@material-ui/core'
import { PlusCircle as IncreaseIcon, MinusCircle as DecreaseIcon } from 'react-feather'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  image: {
    
  },
  amount: {
    padding: theme.spacing(0, 1)
  },
  listItem:{
    width:'100%',
    "& .MuiFormControlLabel-label":{
      width:'100%',
      "& img":{
          width: '50px',
          padding: theme.spacing(0, 1,0,1)
      },
      "& span":{
        width:"410px"
      }
    }
  },
}))

const EquipmentItem = ({ 
  equipment, 
  onSelectItem, 
  decrement, 
  increment, 
  isSelected,
  amount,
  className, ...rest}) => {

  const classes = useStyles()
  const labelId = `checkbox-list-label-${equipment.id}`

  const handleIncrement = () => {
    //console.log('increase')
    increment(equipment)
  }

  const handleDecrement = () =>{
    //console.log('decrease')
    decrement(equipment)
  }

  //console.log('equipment ', equipment)

  return (
    <ListItem 
      className={clsx(className, classes.root)}
      {...rest}
    >
      <FormControlLabel 
        control={(
          <Checkbox
            edge="end"
            checked={isSelected}
            tabIndex={-1}
            disableRipple
            inputProps={{
              'aria-labelledby': labelId,
              'data-testid': `${equipment.id}-checkbox`
            }}
            onChange={()=> {onSelectItem(equipment)}}
            //value={equipment}
          />
        )}
        className={classes.listItem}
        label={(
          <Box 
            display="flex" 
            alignItems="center"
          >
            <img 
              alt={equipment.name} 
              src={equipment.media} 
              data-testid={`${equipment.id}-image`}
            />
            <Tooltip title={equipment.name}>
              <Typography 
                variant="body2" 
                component="span"
                noWrap
              >
                {equipment.name}
              </Typography>
            </Tooltip>
          </Box>
        )}
      />
      <ListItemIcon>
        <IconButton 
          disabled={!isSelected} 
          onClick={handleDecrement}          
          data-testid={`${equipment.id}-decrement`}
        >
          <DecreaseIcon />
        </IconButton>
      </ListItemIcon>
      <Box
        component="span"
        className={classes.amount}
      >
        <Typography variant="body2">
          {amount ?? 0}
        </Typography>
      </Box>
      <ListItemIcon >
        <IconButton 
          disabled={!isSelected} 
          onClick={handleIncrement}
          data-testid={`${equipment.id}-increment`}
        >
          <IncreaseIcon />
        </IconButton>
      </ListItemIcon>
    </ListItem>
  )
}

EquipmentItem.propTypes = {
  className: PropTypes.string,
  equipment: PropTypes.shape({
    id: PropTypes.string,
    name: PropTypes.string,
    media: PropTypes.string,
  }),
  onSelectItem: PropTypes.func,
  decrement: PropTypes.func,
  increment: PropTypes.func,
  isSelected: PropTypes.bool,
  amount: PropTypes.number
}

export default EquipmentItem