import axios from 'src/utils/axios'

class SetsService {

  getAll = () => new Promise((resolve, reject) => {
    axios.get('/api/sets')
      .then((response)=> {
        resolve(response.data)
      })
      .catch((error) => {
        reject({data: [], total: 0, errors: error})
      })
  })

  

  getById = (setId) => new Promise((resolve, reject) => {
    axios.get('/api/sets/getById', { params: { setId } })
      .then((response) => {
        if(!response){
          reject({
            resultFlag: false,
            value: null,
            errorCode: 'Something is wrong.',
            errorMessage: `response is ${response}`,
            activityId: null
          })
          return
        }
        resolve({
          resultFlag: true,
          value: response.data
        })
      })
      .catch((error) => {
        reject({
          resultFlag: false,
          value: null,
          errorCode: 'Something is wrong.',
          errorMessage: error.message,
          activityId: null
        })
      })
  })

  getList = (query, sort, page, limit) => new Promise((resolve, reject) => {
    axios.get('/api/sets/fetch', { params: { 
      query, sort, page, limit } 
    })
      .then((response) => {
        
        if(!response){
          reject({
            resultFlag: false,
            value: null,
            errorCode: 'Something is wrong.',
            errorMessage: `response is ${response}`,
            activityId: null
          })
          return
        }
        resolve({
          resultFlag: true,
          value: response.data
        })
      })
      .catch((error) => {
        reject({
          resultFlag: false,
          value: null,
          errorCode: 'Something is wrong.',
          errorMessage: error.message,
          activityId: null
        })
      })
  })

  insert = (dataItem) => new Promise((resolve, reject) => {
    axios.post('/api/sets/new', dataItem)
      .then((response)=> {
        resolve(response.data)
      })
      .catch((error) => {
        reject({
          resultFlag: false, 
          value: null, 
          errorCode: 'Something is wrong.',
          errorMessage: error.message,
          activityId: null
        })
      })
  })

  update = (dataItem) => new Promise((resolve, reject) => {
    axios.post('/api/sets/update', dataItem)
    .then((response)=> {
      resolve(response.data)
    })
    .catch((error) => {
      reject({
        resultFlag: false, 
        value: null, 
        errorCode: 'Something is wrong.',
        errorMessage: error.message,
        activityId: null
      })
    })
  })

  remove = (dataItem) => new Promise((resolve, reject) => {
    axios.post('/api/sets/remove', {
      ...dataItem
    })
    .then((response)=> {
      resolve(response.data)
    })
    .catch((error) => {
      reject({
        resultFlag: false, 
        value: null, 
        errorCode: 'Something is wrong.',
        errorMessage: error.message,
        activityId: null
      })
    })
  })

  updateEquipmentItems = (id, equipmentItems) => new Promise((resolve, reject) => {
    axios.post('/api/sets/update-equipments', {
      id, 
      equipments: equipmentItems
    })
    .then((response)=> {
      resolve(response.data)
    })
    .catch((error) => {
      reject({
        resultFlag: false, 
        value: null, 
        errorCode: 'Something is wrong.',
        errorMessage: error.message,
        activityId: null
      })
    })
  })
}

const setsService = new SetsService()

export default setsService